<script setup>
import { computed } from 'vue'
import { Icon, ProfilePicture } from '@components'

import { useAppointments } from '@store/appointments.js'
const $appointments = useAppointments()

import { useSettings } from '@store/settings.js'
const $settings = useSettings()

import { useCustomizer } from '@store/customizer.js'
const $customizer = useCustomizer()

import moment from 'moment'

import { getPrice, addSpaceAfterComma, showDurationUnit } from '@utils'

const customer = computed(() => {
    return prebook.customer || {}
})
const business = prebook.business || {}

const calculateTotal = () => {
    return $appointments.selectedService?.price || 0
}

const businessAddress = computed(() => {
    let addressParts = [
        business.address.street,
        business.address.state,
        business.address.city,
        business.address.zip_code,
        business.address.country_name
    ]

    // Filter out empty or undefined parts
    addressParts = addressParts.filter(part => part?.trim?.() !== '')

    console.log(addressParts)

    // Return the formatted address or "N/A" if empty
    return addressParts.length ? addressParts.join(', ') : ""
})


// format time depending on customizer
const formatTime = (time, format) => {
    if ($settings.customizer.time.show_time) {
        return moment(time, format).format('HH:mm')
    } else {
        return moment(time, format).format('hh:mm A')
    }
}

const PAYMENT = prebook.settings.payment
const availablePaymentMethods = computed(() => {
    return PAYMENT.methods.filter(method => method.enabled)
})
</script>

