            <!-- extra amount -->
            <!-- <div class="mt-2 cost-item-1">
                <div>Extras</div>
                <div>$10</div>
            </div> -->

            <!-- subtotal amount -->
            <!-- <div class="mt-3 cost-item-2">
                <div>Sub total</div>
                <div>$110</div>
            </div> -->

            <!-- tax amount -->
            <!-- <div class="mt-2 cost-item-1">
                <div>Salex tax (8%)</div>
                <div>$8.80</div>
            </div> -->

            <!-- divider -->
            <div v-if="$settings.customizer.booking_overview.show_payment_summary" class="my-3 divider"></div>

            <!-- total amount -->
