<script setup>
import { computed, onMounted } from 'vue'
import { Icon, ProfilePicture } from '@components'
import { getPrice, addSpaceAfterComma } from '@utils'
import moment from 'moment'

import { useAppointments } from '@store/appointments.js'
const $appointments = useAppointments()

import { useSettings } from '@store/settings.js'
const $settings = useSettings()

import { useCustomizer } from '@store/customizer.js'
const $customizer = useCustomizer()

const findStaffById = (id) => {
    return $appointments.staffs.find((staff) => staff.staff_id == id)
}

const $prebook = computed(() => {
    return window.prebook || {}
})

// format time depending on customizer
const formatTime = (time, format) => {
    if ($settings.customizer.time.show_time) {
        return moment(time, format).format('HH:mm')
    } else {
        return moment(time, format).format('hh:mm A')
    }
}

// const width = () => {
//     let prebookBookingWrapper = document.querySelector('.main-wrapper')
//     console.log(prebookBookingWrapper.clientWidth)
// }

const getServiceById = (id) => {
    return $appointments.services.find((service) => service.service_id == id)
}

const showServicePrice = (price) => {
    let type = getServiceById($appointments.confirmation.service_id)?.price_type
    if (type == 'fixed') {
        return getPrice(price)
    } else if (type == 'free') {
        return 'Free'
    }
}
</script>

