<script setup>
import { Icon } from '@components'
import { ref } from 'vue'
import { toRGB } from '../utilities'


const props = defineProps({
    classButton: {
        type: String,
    },
    buttonStyle: {
        type: String,
    },
    size: {
        type: String,
        validator(value) {
            return ['sm', 'md', 'lg', 'xl', '2xl'].includes(value)
        },
        default: 'md'
    },
    type: {
        type: String,
        validator(value) {
            return ['primary', 'secondary_gray', 'secondary_color', 'tertiary_gray', 'tertiary_color', 'link_gray'].includes(value)
        },
        default: 'primary'
    },
    radius: {
        type: String,
        validator(value) {
            return ['rounded', 'square'].includes(value)
        },
        default: 'rounded'
    },
    disable: {
        type: Boolean,
    },
    submit: {
        type: Boolean,
    },
})

/**
 * Returns the button size class based on the value of props.size.
 *
 * @return {string} The CSS classes for the button size.
 */
const buttonSizeClass = () => {
    if (props.size == 'sm') {
        return 'gap-2 px-3.5 py-2'
    }
    if (props.size == 'md') {
        return 'gap-2 px-4 py-2.5'
    }
    if (props.size == 'lg') {
        return 'gap-2 px-[18px] py-2.5'
    }
    if (props.size == 'xl') {
        return 'gap-2 px-5 py-3'
    }
    if (props.size == '2xl') {
        return 'gap-3 px-7 py-4'
    }
}

/**
 * Returns the button type class based on the value of the 'type' prop.
 *
 * @return {string} The button type.
 */
const buttonTypeClass = () => {
    if (props.type == 'primary') {
        return 'primary'
    }
    if (props.type == 'secondary_gray') {
        return 'secondary_gray'
    }
    if (props.type == 'secondary_color') {
        return 'secondary_color'
    }
    if (props.type == 'tertiary_gray') {
        return 'tertiary_gray'
    }
    if (props.type == 'tertiary_color') {
        return 'tertiary_color'
    }
    if (props.type == 'link_gray') {
        return 'link_gray'
    }
}

const button = ref(null)

const onFocus = () => {
    const backgroundColor = button.value?.style?.backgroundColor
    const borderColor = button.value?.style?.borderColor

    if (!backgroundColor && !borderColor) return

    button.value.style.boxShadow = `0 0 0 4px ${toRGB(backgroundColor || borderColor, 15) || '#f4ebff'}, 0 1px 2px #1018280d`
}


const removeFocus = () => {
    const backgroundColor = button.value?.style?.backgroundColor
    const borderColor = button.value?.style?.borderColor

    if (!backgroundColor && !borderColor) {
        // console.log('No backgroundColor or borderColor found')
        return
    }

    button.value.style.boxShadow = 'none'
};

</script>



<template>
    <button ref="button" @focus="onFocus" @blur="removeFocus()" v-bind="$attrs" :type="props.submit ? 'submit' : 'button'" class="button-wrapper click-animation" :disabled="props.disable" :class="buttonSizeClass(), buttonTypeClass(), props.radius, props.classButton" :style="props.buttonStyle" @click="handleClick">
        <slot name="before"></slot>
        <slot></slot>
        <slot name="after"></slot>
    </button>
</template>

<style lang="scss" scoped>
