    font-weight: 600;
    overflow: hidden;
    transition: 0.15s ease-in-out;
}

.rounded {
    border-radius: 100px !important;
}

.square {
    border-radius: 8px;
}

.primary {
    border: 1px solid #7F56D9;
    background-color: #7F56D9;
    color: #fff;
    box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);

    &:hover {
        border: 1px solid #6941C6;
