/**
 * Calculates the progress of the current tab based on a given array of tabs.
 *
 * @param {string} tabs - A comma-separated string of tab names.
 * @return {number} The progress of the current tab as a percentage (0-100).
 */
export const progress = (tabs = '') => {
    tabs = tabs.split(',').map(tab => tab.trim())
    const index = tabs.indexOf(useRoute().query.tab || tabs[0])
    return (index + 1) / tabs.length * 100
}


