export const isInteger = (number) => {
    return /^\d+$/.test(number)
}

export const validationCheck = (conditions) => {
    let errors = 0
    conditions.forEach((item) => {
        if (item.condition) {
            errors++
            Toast.warning(item.message)
        }
    })
    return {
        errors: errors > 0 ? true : false
    }
}

