export const addSpaceAfterComma = (text) => {
    return text.replace(/,(\S)/g, ', $1')
}

export const getHashQueryParam = (param) => {
    let queryString = window.location.hash.split('?')[1]
    return new URLSearchParams(queryString).get(param)
}

export const removeHashQueryParam = (param) => {
    let [hash, queryString] = window.location.hash.split('?')
    let params = new URLSearchParams(queryString)
    params.delete(param)
    window.location.hash = params.toString() ? `${hash}?${params}` : hash
};

